import axios, { type AxiosResponse } from 'axios'

import type { ClubId } from 'src/models/Club'
import type { MemberLevel } from 'src/models/ClubMember'
import { type DateProps } from 'src/models/date'

interface GetParameters {
  clubId: ClubId
  date: DateProps
  token: string
}

export const getFeeAmount = async ({ clubId, date, token }: GetParameters) => {
  const response = await axios.get<GetFeeAmountResponseData[]>(
    `${process.env.REACT_APP_APIHOST}/club/${clubId}/fee/amount?semester=${date.semester}&year=${date.year}`,
    {
      headers: { Authorization: token }
    }
  )
  return response.data
}

export interface GetFeeAmountResponseData {
  id: number
  level: MemberLevel
  amount: number
}

interface PostParameters {
  clubId: ClubId
  date: DateProps
  data: PostFeeAmountRequestData
  token: string
}

export interface PostFeeAmountRequestData {
  level: MemberLevel
  amount: number
}

export const postFeeAmount = async ({ clubId, date, data, token }: PostParameters) => {
  await axios.post<null, AxiosResponse<null>, PostFeeAmountRequestData>(
    `${process.env.REACT_APP_APIHOST}/club/${clubId}/fee/amount?semester=${date.semester}&year=${date.year}`,
    data,
    {
      headers: { Authorization: token }
    }
  )
}

export interface PutFeeAmountRequestData {
  amount: number
}

interface PutParameters {
  clubId: ClubId
  data: PutFeeAmountRequestData
  editFeeId: number
  token: string
}

export const putFeeAmount = async ({ clubId, data, editFeeId, token }: PutParameters) => {
  await axios.put<null, AxiosResponse<null>, PutFeeAmountRequestData>(
    `${process.env.REACT_APP_APIHOST}/club/${clubId}/fee/amount/${editFeeId}`,
    data,
    {
      headers: { Authorization: token }
    }
  )
}

interface DeleteParameters {
  clubId: ClubId
  deleteId: number
  token: string
}

export const deleteFeeAmount = async ({ clubId, deleteId, token }: DeleteParameters) => {
  await axios.delete<null, AxiosResponse<null>, null>(
    `${process.env.REACT_APP_APIHOST}/club/${clubId}/fee/amount/${deleteId}`,
    {
      headers: { Authorization: token }
    }
  )
}
